.work {
  margin-top: 94px;

  &__image {
    position: relative;
  }

  &__label {
    position: absolute;
    bottom: 0;
    background-color: map-get($colors, 'boxed');
    width: 100%;
  }

  &__text {
    padding: 27px 0;
    color: map-get($colors, 'light');
    @include font(14px, 700, 1.1, 0.03em);
    text-align: center;
    text-transform: uppercase;
  }
}

//PHONE, TABLET

@media screen and (max-width: calc($breakpointDesktop - 1px)) {
  .work {
    display: none;
  }
}
